@import url(https://fonts.googleapis.com/css2?family=Covered+By+Your+Grace&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{position:relative;width:100%;height:100vh;font-family:"Lato", sans-serif;overflow:hidden}.App{position:relative;transition:all 0.5s ease;height:100vh;background-color:#11052c}.App .overlay{position:absolute;top:0;left:0;width:100%;height:100%;z-index:1;-webkit-filter:blur(3px) grayscale(75%);filter:blur(3px) grayscale(75%);background-position:center;background-size:cover;background-repeat:no-repeat;-webkit-clip-path:circle(0% at 50% 50%);clip-path:circle(0% at 50% 50%);transition:all 0.5s ease}.App .overlay::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);background-blend-mode:multiply}.library-active{margin-left:20%}h1{font-size:1rem}h2{color:#333}h3,h4{color:#646464;font-weight:400}.song-container{position:relative;z-index:10;min-height:60vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.song-container img{width:25vw;height:25vw;object-fit:cover;object-position:center;border-radius:50%;transition:all 2s ease;box-shadow:2px 2px 50px #000;border:10px solid #fff}.song-container h2{padding:3rem 1rem 1rem 1rem;color:#fff;font-family:"Covered By Your Grace", cursive;letter-spacing:2px}.song-container h3{font-size:1rem;color:#9c9c9c}@media screen and (max-width: 768px){.song-container img{width:60vw;height:60vw}}@-webkit-keyframes rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.rotateSong{-webkit-animation:rotate 20s linear forwards infinite;animation:rotate 20s linear forwards infinite}.player{position:relative;z-index:10;min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between;color:#fff}.time-control{width:50%;display:flex;align-items:center}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:1rem}.play-control{position:relative;display:flex;justify-content:space-between;align-items:center;padding:1rem;transition:all 0.5s ease;width:40%}.play-control svg{cursor:pointer}.play-control .audio-range{-webkit-appearance:none;background:rgba(255,255,255,0.5);border-radius:10px;outline:none;height:10px;cursor:pointer}.play-control .audio-range::-webkit-slider-thumb{background:#fff;border-radius:50%;-webkit-transform:scale(1.1);transform:scale(1.1)}.track{width:100%;height:1rem;position:relative;overflow:hidden;border-radius:1rem;box-shadow:2px 2px 50px #000}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(0%);transform:translateX(0%);padding:1rem;pointer-events:none}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}.media-controls{position:relative;width:100%;display:flex;justify-content:space-around;align-items:center}.audio-controls{width:100%;display:flex;justify-content:space-around;align-items:center}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:100%;flex-direction:column;grid-gap:2rem;gap:2rem}.play-control .audio-controls{width:60%}}.library{position:fixed;z-index:10;box-shadow:2px 2px 50px #000;top:0;left:0;width:20rem;height:100%;overflow:scroll;background:white;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library .library-nav{display:flex;justify-content:space-between;align-items:center;padding:2rem}.library-song{position:relative;z-index:10;display:flex;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer;transition:all 0.75s ease-out}.library-song img{width:30%}.library-song:hover{background:#ebebeb}.song-description{padding-left:1rem}.song-description h3{font-size:1rem}.song-description h4{font-size:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.7) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.7);border-radius:20px;border:transparent}.selected{background:#c2d0fd}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{position:relative;z-index:10;min-height:10vh;display:flex;justify-content:space-around;align-items:center;color:#fff}nav h1{font-size:2rem;font-weight:bold;font-family:"Rock Salt", cursive}nav button{position:relative;background:transparent;border:none;cursor:pointer;padding:0.5rem;border:2px solid #fff;transition:all 0.3s ease;color:#fff}nav button:hover{background:#fff;color:#000}nav .library-active{background:#fff;color:#000}@media screen and (max-width: 768px){nav button{z-index:15}}

